import React, { Component } from 'react';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import Navigation from './components/Navigation/Navigation';
import Footer from './components/Footer/Footer';
import './App.css';
import Home from './routes/Home';
import About from './routes/About';
import Response from './routes/Response';
//You must add your own API key here from Clarifai.
const app = new Clarifai.App({
 apiKey: '194d225811c04278b0d6377bc90c9380'
});

const particlesOptions = {
    "particles": {
      "color":"#3ca9d1",
        "number": {
            "value": 60,
            "density": {
                "enable": true,
                "value_area": 1500
            }
        },
        "line_linked": {
            "enable": true,
            "opacity": 0.2,
            "color":"#3ca9d1",
            "width":"1.5"
        },
        "move": {
            "direction": "right",
            "speed": 0.05
        },
        "size": {
            "value": 1
        },
        "opacity": {
            "anim": {
                "enable": true,
                "speed": 1,
                "opacity_min": 0.05
            }
        }
    },
    "interactivity": {
        "events": {
            "onclick": {
                "enable": true,
                "mode": "push"
            }
        },
        "modes": {
            "push": {
                "particles_nb": 1
            }
        }
    },
    "retina_detect": true
}


class App extends Component {
  constructor() {
    super();
    this.state = {
      input: '',
      imageUrl: '',
      box: [],
      route: 'about',
      detect: '',
      data:[],
      }
    }
  
    determineFood = (data) => {

      return  data.outputs[0].data.concepts.slice(0,3);
     
    }


    determinePeople = (data) => {
    const People =  data.outputs[0].data.regions;
    const image = document.getElementById('inputimage');
    const height = Number(image.height);
    const width = Number(image.width);
    let boxes =[];
    let name = [];
    People.forEach(person => {
      name.push(person.data.concepts[0])
      const clarifaiFace = person.region_info.bounding_box;
      const box=
      {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height)
    }
    boxes.push(box);
    });
    this.displayData(name)
    return (this.displayBox(boxes),this.displayData(name))
  }

  displayBox = (box) => {
    this.setState({box: box})
  }
  displayData = (data) =>{
  this.setState({data: data})
  }

  onInputChange = (event) => {
    this.setState({ input: event.target.value})
  }
  onDetectChange = (detect) => {
    this.setState({ detect: detect})
  }

  onRouteChange = (route) => {
    this.setState({route: route})
    this.setState({detect:''})
  }
  onRestart = () => {
    this.onRouteChange('home')
    this.setState({data:[]})
    this.setState({box:[]})
    this.setState({input:''})
  }

  onButtonSubmit = () => {
    const { input } = this.state;
    const { detect} = this.state;
    this.setState({ imageUrl: input});
    this.setState({route: 'response'});
    // predict the contents of an image by passing in a url
    if(detect==="face"){
    app.models.predict(Clarifai.CELEBRITY_MODEL, input)
    .then(response => this.determinePeople(response))
    .catch(error => console.log(error));
    }
    else if(detect==="food"){
    app.models.predict(Clarifai.FOOD_MODEL, input)
      .then(response => this.displayData(this.determineFood(response)))
      .catch(error => console.log(error))}
  }
 
  render() {
    const { route,  box, imageUrl, detect,data,input } = this.state;
    return (
      <div className="App">
       <Particles className="particles"params={particlesOptions}/>
        <div className="content">
          <Navigation  onRouteChange={this.onRouteChange}/>
            {route==="about"
              ?
              <About onRouteChange={this.onRouteChange}/>
              :(route==="home"&&data==''
              ?
              <Home detect={detect} onButtonSubmit={this.onButtonSubmit} onInputChange={this.onInputChange} onDetectChange={this.onDetectChange}  onRouteChange={this.onRouteChange} input={input}/>
              :
              <Response box={box} imageUrl={imageUrl} data={data} detect={detect} onRestart={this.onRestart}/>
              )
            }
            <Footer/>
        </div>
    </div>        
    );
  }
}



export default App;
